import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import MainIcon from "../images/main.jpg"
import More from "../components/More"

import Header from "../components/header"
import "../Style/layout.css"
import "../Style/bootstrap.css"
import "../Style/litle-style.css"
import Footer from "../components/Footer"

const About = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <div className="hero_area">
        <Header siteTitle={data.site.siteMetadata?.title || `Title`} />

        <div className="hero_bg_box">
          <img src={MainIcon} alt=""></img>
        </div>
        <main>{children}</main>
      </div>
      <p className="newcolor">Привет страница о нас </p>
      <div>
        <p className="newcolor">
          Группа компании «ALTYN JULDYZ» — это многоотраслевая компания, которая
          успешно оперирует в различных сферах бизнеса в Республике Казахстан.
          Наша компания имеет многолетний опыт и является одним из ведущих
          предприятий на рынке Казахстана.
          <br />
          Производство и торговля: Мы гордимся нашей швейно-обувной фабрикой,
          которая производит высококачественную одежду и обувь, учитывая
          последние модные тенденции и потребности клиентов. Кроме того, мы
          также предоставляем широкий ассортимент средств индивидуальной защиты,
          строительных инструментов и материалов, а также строительное
          оборудование и спортивные товары для различных отраслей.
          <br />
          Строительство и проектирование: Наша компания специализируется в
          жилищном строительстве и проектировании современных и уникальных жилых
          объектов. Кроме того, мы предлагаем услуги по аренде складских
          помещений класса А и С с высоким уровнем безопасности и комфорта для
          вашего бизнеса.
          <br />
          Школьное оборудование и кабинеты: Интерактивные панели и OPS
          Моноблоки, ПК, планшеты 3D принтеры Цифровые лаборатории
          Робототехнические наборы и оборудование Лабораторное оборудование и
          демонстрационные приборы Программное обеспечение Учебная Учебная
          мебель
          <br />
          Сельское хозяйство: Мы уделяем особое внимание развитию сельского
          хозяйства и являемся ответственным разведением КРС «Абердин Ангус», а
          также МРС «Романовские овцы». Наш интенсивный яблочный сад
          обеспечивает высокоурожайные и качественные плоды, а в сфере
          рыбоводства мы стремимся к предоставлению свежей и здоровой рыбы.
          <br />
          Аренда VIP авто: Мы предлагаем услуги аренды VIP автомобилей, чтобы
          обеспечить вашу комфортабельность и роскошь во время путешествий и
          деловых встреч.
          <br />
          Кинематография: Мы активно развиваем кинематографическую индустрию и
          участвуем в создании уникальных и качественных кино проектов, которые
          вдохновляют и развлекают наших зрителей. Группа компании «ALTYN
          JULDYZ» стремится к постоянному совершенствованию и предоставлению
          нашим клиентам только самых лучших продуктов и услуг. Мы гордимся
          нашей командой опытных профессионалов, которые являются ключевым
          фактором нашего успеха. Наша цель — удовлетворить потребности наших
          клиентов и принести вклад в процветание общества. Выбирайте Группа
          компании «ALTYN JULDYZ» — ваш надежный партнер в различных отраслях
          бизнеса
        </p>
      </div>
      <More />
      <Footer/>
    </>
  )
}

export default About
